body{
    margin:0;
    padding:0;
    /* background-color: red; */
}

.Container-Home{
    display: flex;
    justify-content: center;
    gap: 5rem;
    height: 80vh;
    background-color: white;
    background-image: url(../asset/bgg1.png);
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
} 


.Content-Home{
    display: flex;
    height: 300px;
    width: 300px;
    justify-content: center;
    margin: auto 0;
    border: 5px solid rgb(221, 44, 197);
    border-radius: 100%;
    /* background-color: dark; */
    /* border-radius: 2px; */
}

.text{
    align-self: center;
    text-align: center;
}

.text p{
    font-weight: 600;
    
}

.p{
    text-align: center;
    font-size: 1.75rem;
    margin-bottom: 1rem;
    margin-top: 20px;
    color: black;
}

.gede{
    font-size: 40px;
    text-align: center;
    color: rgb(221, 44, 197);
}

.pp{
    text-align: center;
    font-size: 1.75rem;
    margin-bottom: 1rem;
    color: black    ;
}
.icon{
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    gap: 1rem;
    color: white;
    /* color: rgb(221, 44, 197); */
    /* font-size: 50px; */
}

.icon button{
    display: flex;
    font-size: 30px;
    background-color: white;
    /* color: rgb(221, 44, 197); */
}

.btn-container{
    display: flex;
    justify-content: center;
    gap: 1rem;
    /* border: 2px; */
}

.btn-color{
    color: black;
    border: rgb(0, 0, 0) 2px solid;
    cursor: pointer;
    border-radius: 15px;
    font-size: 20px;
    font-weight: 500
}

.btn-color:hover{
    cursor: pointer;
    background-color: rgb(221, 44, 197);
    color: white;
}

.nm{
    font-size: 15px;
}


