@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body{
    margin: 0;
    padding: 0;
    font-family: 'poppins' , sans-serif;
    /* background-color: var(--body-color); */
}

.Container{
    background-color: rgb(221, 44, 197);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    justify-items: center;
    padding: 15px;
    width: 100%;
    /* margin-bottom: 200px; */
    
}
.Container .Logo{
    width: 50px;
    height: 50px;
    /* background-color: rgb(221, 44, 197); */
    border-radius: 50%;
    border: none;
    display: flex;
    margin-top: 6px;
    margin-left: 5px;
    cursor: pointer;
    /* flex-direction: row; */
    /* justify-content: space-between; */
    /* justify-items: center; */
}

.Logo .Name{
    display: flex;
    text-decoration: none;
    color: whitesmoke;
    right: 50px;
    /* margin-left: px; */
    margin-right: 100px;
    margin-top: 5px;
    font-size: 25px;
}

.Navbar{
    list-style: none;
    margin-right: 40px;
}
.Navbar ul{
    display: flex;
    /* flex-direction: row; */
    /* justify-content: flex-end; */
}
.Navbar ul li{
    list-style: none;
    /* margin-right: 50px; */
    width: 100px;
    /* right: 20px; */
    /* left: 30px; */
    margin-left: 30px;
    margin-top: 12px;
}
.Navbar ul li a{
    text-decoration: none;
    color:white;
    font-size: 20px;
    /* margin-left:  */
    /* margin-right: 100px;  */
    /* margin-top: 20px; */

}
.Navbar ul li a:hover{
    color: rgb(0,0,0) ;
    cursor: pointer;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(0,0,0);
    /* transition: 2s;
    transform: 1s;
    font-size: 30px; */
}

.contact{
